<template>
    <div>
        <da-equal-panel height="440px">
            <div slot="left">
                <da-tree :data-lists="subitemTreeData" :default-expand="true" @currentChange="subitemTreeChange"></da-tree>
            </div>
            <div slot="right">
                <el-radio-group v-model="subitemTime" @change="subitemTimeChange">
                    <el-radio :label="3">最近24小时</el-radio>
                    <el-radio :label="6">最近一周</el-radio>
                    <el-radio :label="9">自定义</el-radio>
                </el-radio-group>
                <da-time-range v-model="timeSpan" v-if="subitemTime==9" :show-time="[2,1,0]" @change="timeSpanSelect"></da-time-range>
                <da-radio-block v-model="rdbSubitem" :data-lists="subitemList" label="subitemName" value-key="id" @change="subitemChange"></da-radio-block>
                <da-line-chart id="chrtEnergyConsumptiont" :date-time="timeSpan" :data-lists="energyConsumptiontData"></da-line-chart>
            </div>
            <div style="width: 200px;">
                <p>分析结论</p>
                <!-- <P>A相电压最大值：{{maxValue}}</P>
            <P>最大值发生时间：{{maxTime}}</P>
            <P>A相电压最小值：{{minValue}}</P>
            <P>最小值发生时间：{{minTime}}</P>
            <P>超限率：{{overrunRate}}</P> -->
                <p>不同数据项的分析算法和结论不一样</p>
                <p>根据数据，以及行业内通用的解决方案，给出 建议给用户，例如功率因数偏低，可以采用XXX 方法提高功率因数等。
                </p>
            </div>
        </da-equal-panel>
        <da-panel title="总成本分析" width="100%" height="auto" min-height="200px" padding="0 10px" ref="panelCount">
            <el-form label-width="90px" :model="addForm" :rules="rules" ref="addForm">
                <el-form-item label="报警详情" prop="name">
                    <el-input auto-complete="off" v-model="addForm.name"></el-input>
                </el-form-item>
                <el-form-item label="表计名称" prop="type">
                    <el-input auto-complete="off" v-model="addForm.type"></el-input>
                </el-form-item>
                <el-form-item label="采集项名称" prop="no">
                    <el-input auto-complete="off" v-model="addForm.no"></el-input>
                </el-form-item>
                <el-form-item label="报警时间" prop="orgId">
                    <el-input auto-complete="off" v-model="addForm.orgId"></el-input>
                </el-form-item>
                <el-form-item label="报警确认时间" prop="model">
                    <el-input auto-complete="off" v-model="addForm.model"></el-input>
                </el-form-item>
                <el-form-item label="确认人" prop="ratedPower">
                    <el-input auto-complete="off" v-model="addForm.ratedPower"></el-input>
                </el-form-item>
            </el-form>
        </da-panel>
    </div>
</template>

<script>
export default {
  name: "equip",
  data() {
    return {
      subitemTreeData: [], // 树
      tableList: {}, // 数据列表
      subitemTime: "",
      rdbSubitem: "",
      timeSpan: "",
      subitemList: [],
      colsData: [
        // 表头
        { label: "报警详情", prop: "name", align: "left", sortable: true },
        { label: "表计名称", prop: "type", align: "left", sortable: true },
        { label: "采集项名称", prop: "no", align: "left", sortable: true },
        { label: "报警时间", prop: "orgId", align: "left", sortable: true },
        { label: "报警确认时间", prop: "model", align: "left", sortable: true },
        { label: "确认人", prop: "ratedPower", align: "left", sortable: true }
      ],

      energyConsumptiontData: [],
      addForm: {},
      rules: this.eachValidate({})
    };
  },
  computed: {},
  watch: {},
  methods: {
    initData() {},
    // 切换树事件
    subitemTreeChange() {},
    subitemTimeChange() {},
    timeSpanSelect() {},
    // radio change
    subitemChange() {},
    loadData() {
      this.initData();
      this.getTbList();
    },
    getTbList() {}
  },
  mounted() {
    this.loadData();
  }
};
</script>



